<template>
  <v-responsive>
    <v-carousel style="background: grey" hide-controls>
      <span class="headline">
        <v-dialog v-model="dialog" fullscreen hide-overlay transition="dialog-bottom-transition">
          <v-btn slot="activator" fab light color="white" v-if="owner" icon>
            <v-icon>person_add</v-icon>
          </v-btn>
          <v-card>
            <v-toolbar dark color="primary">
              <v-toolbar-title>Profile pictures</v-toolbar-title>
              <v-spacer></v-spacer>
              <v-toolbar-items>
                <v-btn dark flat @click.native="dialog = false">Cancel</v-btn>
                <v-btn dark flat @click.native="submit">Save</v-btn>
              </v-toolbar-items>
            </v-toolbar>
            <v-layout justify-center>
      				<v-flex class="text-xs-center" xs12 md10>
                <v-layout v-if="gallery && gallery.length" justify-center>
                  <v-flex xs6 md4 lg3
                    v-for="(img, key) in gallery"
                    :key="key"
                  >
                    <img :src="'@/assets/images/'+img.picture" height="300" />
                  </v-flex>
                </v-layout>
      					<img v-if="imageFile" :src="imageFile" height="200" />
      					<v-text-field label="Select Image" @click='browse' v-model='imageName' prepend-icon='attach_file'></v-text-field>
      					<input
      						type="file"
      						style="display: none"
      						ref="image"
      						accept="image/*"
      						@change="loadFile"
      					>
                <v-btn :disabled="!imageFile" @click="addFile" color="primary">Add</v-btn>
      				</v-flex>
            </v-layout>
          </v-card>
       </v-dialog>
      </span>
      <v-carousel-item
        v-for="(item,i) in items"
        :key="i"
        :src="item.src"
      ></v-carousel-item>
    </v-carousel>
  </v-responsive>
</template>

<script>
  import axios from 'axios'
  import FormData from 'form-data'

  export default {
    props: ['owner', 'id'],

    data: () => ({
      gallery: null,
      dialog: false,
      imageName: '',
      imageURL: null,
      imageFile: null,
      items: ["/assets/logo.png"],
    }),

    beforeMount() {
    },

    methods: {
      submit() {
        axios.get('api/users/pictures', {
          params: this.$user
        }).then(response => {
          if (response.data) {
            console.log(response.data)
            this.gallery = response.data
          }
        })
      },
      browse () {
           this.$refs.image.click ()
       },
      loadFile (e) {
        const fr = new FileReader()
        const file = e.target.files[0]

        fr.onloadend = () => {
          this.imageFile = fr.result
        }
        fr.readAsDataURL(file)
  		},
      addFile() {
        if (this.imageFile) {
          console.log(this.imageFile)
          axios.post('api/users/addpicture', {
            id: this.$user.id,
            token: this.$user.token,
            picture: this.imageFile
          }).then(response => {
            if (response.data) {
              console.log("--image sent--")
            }
          })
        }
      },
    },
  }
</script>

